@import 'lib/_reset';
// @import 'lib/_normalize';

// Config

@import 'config/_variables';
@import 'config/_utils';
@import 'config/_fonts';

// Global style
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: inherit;
}

#content {
  position: relative;
  max-width: 100vw;
  overflow: hidden;
}

.main-wrapper {
  max-width: $gridSize;
  margin: 0 auto;

  @include desktop-large {
    max-width: $gridSizeLarge;
  }

  @include laptop {
    margin: 0 30px;
  }

  @include mobile {
    margin: 0 15px;
  }

  @include tablet {
    margin: 0 24px;
  }
}

img {
  max-width: 100%;
}

a {
  color: inherit;
}

.mobile-only {
  display: none;

  @include mobile {
    display: block;
  }
}
.title__wrapper{
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.title__number{
  font-family: "Misto", sans-serif;
  font-size: 109px;
  line-height: 131px;
  letter-spacing: 10.9px;
}
.title--montserrat{
  font-family: "Montserrat",sans-serif;
}
.title--misto{
  font-family: "Misto", sans-serif;
}
.title--optima{
  font-family: "Optima", sans-serif;
}

.descritpion,
.description p{
  
  font-family: "Optima",sans-serif;
  font-size: 18px;
  line-height: 22px;
  font-weight: normal;
  color: $white;

  strong{
    font-weight: bold;
  }
}

// Modules

@import 'modules/_header';
@import 'modules/_footer';
@import 'modules/_cta';

// FLEXIBLES

@import 'flexible/_splash';
@import 'flexible/_about';
@import 'flexible/_apply';
@import 'flexible/_organisation';
@import 'flexible/_members';
@import 'flexible/_contact-form';